@charset "UTF-8";
/*
  This boilerplate is based in ITCSS and SMACSS
*/
/* -------------------------------- 

Primary style

-------------------------------- */
*,
*::after,
*::before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  font-family: "Open Sans", sans-serif;
  color: #2c343b;
  background-color: white;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

img,
video {
  max-width: 100%;
}

@font-face {
  font-family: "bold";
  src: url("../fuentes/Kanit/EncodeSans-Bold.ttf") format("truetype");
}

@font-face {
  font-family: "light";
  src: url("../fuentes/Kanit/EncodeSans-Light.ttf") format("truetype");
}

@font-face {
  font-family: "mediun";
  src: url("../fuentes/Kanit/EncodeSans-Medium.ttf") format("truetype");
}

@font-face {
  font-family: "regular";
  src: url("../fuentes/Kanit/EncodeSans-Regular.ttf") format("truetype");
}

@font-face {
  font-family: 'icomoon';
  src: url("../icon/icomoon.eot?wgwy6n");
  src: url("../icon/icomoon.eot?wgwy6n#iefix") format("embedded-opentype"), url("../icon/icomoon.ttf?wgwy6n") format("truetype"), url("../icon/icomoon.woff?wgwy6n") format("woff"), url("../icon/icomoon.svg?wgwy6n#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

