.main-nav {
  background: #44200e;
  position: fixed;
  top: 0;
  right: -12em;
  width: 12em;
  padding: 1em;
  height: 100vh;
  z-index: 200;
  transition: all .3s;
}

.main-nav.mostrar {
  right: 0;
}

@media screen and (min-width: 1024px) {
  .main-nav {
    background: transparent;
    width: 50%;
