    color: #44200e;
  }
}

.main-menu__link:hover {
  color: #c0aa83;
  transition: ease-in-out .25s;
}

body {
  position: relative;
  right: 0;
  transition: all .3s;
}

body.mostrar {
  right: 12em;
}

@media screen and (min-width: 1024px) {
  body.mostrar {
    right: 0;
  }
}

.button {
  font-size: 1.2em;
  transition: all .25s;
  font-family: "bold", sans-serif;
  border-radius: 8px;
}

.button.primary {
  background: #c0aa83;
  color: #242424;
  line-height: 1.9em;
  padding: 10px 40px;
  border: 2px solid #c0aa83;
  transition: all .25s;
}

.button.primary:hover {
  border-color: #242424;
  background: transparent;
  color: #242424;
  transition: all .25s;
  text-decoration: none;
}

.button.secondary {
  background: #c0aa83;
  color: #242424;
  line-height: 1.9em;
  padding: 10px 40px;
  border: 2px solid #c0aa83;
  transition: all .25s;
}

.button.secondary:hover {
  border-color: #242424;
  background: #f4f4f4;
  color: #242424;
  transition: all .25s;
  text-decoration: none;
}

.btnproducto {
  font-family: "regular", sans-serif;
  font-weight: 700;
  font-size: .9em;
  padding: 5px;
