/* Style the tab */
.tab {
  overflow: hidden;
  background-color: #f6f6f6;
  border: none;
}

/* Style the buttons inside the tab */
.tab button {
  font-family: "bold", sans-serif;
  background-color: #f6f6f6;
  float: left;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  transition: 0.3s;
  font-size: 17px;
}

/* Change background color of buttons on hover */
.tab button:hover {
  background-color: #ddd;
}

/* Create an active/current tablink class */
.tab button.active {
  background-color: #c0aa83;
}

/* Style the tab content */
.tabcontent {
  display: none;
  padding: 6px 12px;
  border-top: none;
}

.tabcontent h3 {
  font-family: "bold", sans-serif;
  padding: 50px 0 10px 0;
  margin: 0px;
  text-align: left;
}

.tabcontent p {
  font-family: "light", sans-serif;
  font-weight: 500;
  margin: 0px;
  text-align: left;
}

.paginador-main .pagination .page-item .page-link {
  font-family: "bold", sans-serif;
  color: black;
  padding: 15px;
}

.page-item.active .page-link {
  border: 1px solid #dee2e6;
  background: #c0aa83;
}

.page-link:focus {
  z-index: 2;
  outline: none;
  box-shadow: none;
}

